@import "TrackList/TrackList";

.albums {
  // .albums__container

  &__container {
    @media (min-width: $desktop) {
      padding: 0;
    }
  }

  // .albums__album

  &__album {
    display: flex;
    flex-wrap: wrap;
    gap: rem(20);
    justify-content: center;
  }

  // .albums__music-card

  &__music-card {
    flex: 0 1 rem(370);
  }

  // .albums__track-list

  &__track-list {
    flex: 0 1 rem(340);
    padding-left: em(30, 18);
    font-size: rem(18);

    @media (max-width: 1098.98px) {
      padding-right: em(70, 18);
    }

    @media (max-width: $mobile) {
      flex-basis: rem(370);
      padding-right: 0;
    }

    @media (max-width: $mobileSmall) {
      padding: 0 em(30, 18) 0 em(60, 18);
    }
  }

  // .albums__listen-on

  &__listen-on {
    ul {
      @media (max-width: 1098.98px) {
        grid-template-columns: repeat(4, rem(185));
      }

      @media (max-width: $tablet) {
        grid-template-columns: repeat(2, rem(185));
      }

      @media (max-width: $mobileSmall) {
        grid-template-columns: repeat(1, rem(185));
      }
    }
  }

  // .albums__swiper

  &__swiper {
    @media (max-width: $mobileSmall) {
      .swiper-button-next,
      .swiper-button-prev {
        &::after {
          background-color: transparent;

          @media (prefers-color-scheme: light) {
            color: var(--mainColor);
          }
        }
      }

      .swiper-button-prev {
        &::after {
          left: rem(-23);
        }
      }

      .swiper-button-next {
        &::after {
          right: rem(-23);
        }
      }
    }
  }
}
